<template>
  <div>
    <!-- 讲师详情页面======================================================================= -->
    <div class="zmb_bgd">
      <van-icon name="arrow-left" @click="back" />
      <div class="zmb_title">讲师详情</div>
    </div>
    <div class="zmb_card">
      <img :src="teacherdata.avatar" alt="" />
      <div class="zmb_txt">
        <p>{{ teacherdata.real_name }}</p>
        <p>男 30年教龄</p>
      </div>
      <div class="zmb_guanzhu">
        <button
          class="zmb_guanzhu_btn"
          v-show="isShow == 2"
          @click="onfollowT(teacherdata.id)"
        >
          关注
        </button>
        <button
          class="zmb_guanzhu_btn"
          v-show="isShow == 1"
          @click="onfollowF(teacherdata.id)"
        >
          不再关注
        </button>
      </div>
    </div>
    <div class="zmb_introduce">
      <van-tabs v-model="activeName">
        <van-tab title="讲师介绍">
          <ul>
            <li>
              <p>教学年龄</p>
              <div>44年</div>
            </li>
            <li>
              <p>授课价格</p>
              <div>400学习币</div>
            </li>
            <li>
              <p>老师简介</p>
              <div>{{ teacherdata.introduction }}</div>
            </li>
          </ul>
        </van-tab>
        <van-tab title="主讲课程"></van-tab>
        <van-tab title="学员评价"></van-tab>
      </van-tabs>
      <router-view></router-view>
      <van-button
        class="zmb_yvyue_btn"
        type="primary"
        color="#EB6100"
        block
        @click="onorder"
        >立即预约</van-button
      >
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeName: "a",
      teacherdata: {},
      isShow: false,
    };
  },
  mounted() {
    this.getdata();
  },
  methods: {
    //返回
    back() {
      this.$router.go(-1);
    },
    //获取老师数据
    getdata() {
      this.$APP.teacherInfo(this.$route.query.id).then((res) => {
        console.log(res);
        this.teacherdata = res.data.data.teacher;
        this.isShow = res.data.data.flag;
      });
    },
    onfollowT(id) {
      this.isShow = true;
      this.$APP.fllow(id).then((res) => {
        console.log(res);
        this.getdata();
      });
    },
    onfollowF(id) {
      this.isShow = false;
      this.$APP.fllow(id).then((res) => {
        this.getdata();
      });
    },
    //预约
    onorder(){
      this.$router.push({
        path:'/teacherorder',
        query:{
          teacherdata:this.teacherdata
        }
      })
    }
  },
  computed: {},
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}
body {
  background: rgb(245, 245, 245);
}
.zmb_bgd {
  width: 100%;
  height: 3rem;
  background: #5facf9;
  display: flex;
  padding: 0.5rem 0;
  color: white;
}
.zmb_back {
  width: 1rem;
  margin-left: 1rem;
}
.zmb_title {
  flex: 1;
  text-align: center;
}
.zmb_card {
  font-size: 0.5rem;
  width: 95%;
  height: 4rem;
  margin: -1.8rem auto 0;
  background: white;
  border-radius: 0.5rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0 1rem;
  box-sizing: border-box;
}
.zmb_card img {
  width: 2.5rem;
  height: 2.5rem;
  margin-right: 0.5rem;
}
.zmb_txt {
  flex: 1;
}
.zmb_guanzhu_btn {
  width: 3rem;
  height: 1rem;
  border: 1px solid #eb6100;
  border-radius: 1rem;
}
.zmb_introduce {
  margin-top: 0.5rem;
  width: 100%;
  background: white;
  font-size: 0.8rem;
}
.zmb_yvyue_btn {
  position: fixed;
  bottom: 0;
  left: 0;
}
ul li {
  display: flex;
  margin-top: 1rem;
  font-size: 0.7rem;
  color: gray;
}
ul li p {
  width: 30%;
  text-align: center;
}
</style>